import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, message } from 'antd';
import { createStudyGrading, updateStudyRanking, getStudyRankingById } from 'services/studyRanking';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    graded: '',
    englishName: '',
    fromScores: '',
    toScores: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formLearningRank' })
class FormRankLearning extends Component {
  state = {
    rankId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ rankId: match.params.id });
      this.getRankingById(match.params.id);
    }
  };

  getRankingById = async id => {
    const res = await getStudyRankingById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };


  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        try {
          if (!this.state.rankId) {
            await createStudyGrading(values);
          } else {
            await updateStudyRanking({ id: this.state.rankId, ...values });
          }
          this.props.history.goBack();
        } catch (error) {
          message.error(`Có lỗi xãy ra!`);
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { rankId } = this.state;
    return (
      <Card title={rankId ? 'Cập nhật xếp hạng học lực' : 'Thêm xếp hạng học lực'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Xếp loại" {...formItemLayout}>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
              {getFieldDecorator('labelInEnglish', {
                initialValue: initialValue.labelInEnglish,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm" hasFeedback>
              {getFieldDecorator('fromGrade', {
                initialValue: initialValue.fromGrade,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến điểm" hasFeedback>
              {getFieldDecorator('toGrade', {
                initialValue: initialValue.toGrade,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#/rank-learning/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormRankLearning {...props} />
  </Page>
));



