import { request } from 'utils';

const prefix = '/api/categories';

export const getStudyGradings = ({
  label,
  labelInEnglish,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/StudyGrading',
    method: 'GET',
    params: {
      label,
      labelInEnglish,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });


export const createStudyGrading = ({
  label,
  labelInEnglish,
  fromGrade,
  toGrade
} = {}) =>
  request({
    prefix,
    url: '/StudyGrading',
    method: 'POST',
    params: {},
    data: {
      label,
      labelInEnglish,
      fromGrade,
      toGrade
    }
  });

  
export const getStudyRankingById = (id) =>
  request({
    prefix,
    url: `/StudyGrading/${id}`,
    method: 'GET',
    params: {},
    data: {}
  });


export const updateStudyRanking = ({
  id,
  label,
  labelInEnglish,
  fromGrade,
  toGrade
} = {}) =>
  request({
    prefix,
    url: `/StudyGrading/${id}`,
    method: 'PUT',
    params: {},
    data: {
      id,
      label,
      labelInEnglish,
      fromGrade,
      toGrade
    }
  });


export const deleteStudyRanking = id =>
  request({
    prefix,
    url: `/StudyGrading/${id}`,
    method: 'DELETE',
    params: {},
    data: {}
  });




